import axios, { AxiosRequestConfig, AxiosRequestHeaders, AxiosResponse } from 'axios'
import { ElMessage } from 'element-plus'
import $localStorage from "@/store/localStorage"
import router from "@/router";

//与后台约定统一返回对象
interface IResponseData<T> {
    message:string
    data:T
    code: number
}

class Http {
    service
    constructor() {
        this.service = axios.create({
            baseURL: 'https://user.xurongze.com/dm-user',
            timeout: 5000 //统一请求超时
        })

        // 请求拦截
        this.service.interceptors.request.use((config: AxiosRequestConfig) => {
            //请求头中赋值token
            (config.headers as AxiosRequestHeaders).token = $localStorage.getToken();
            return config
        }, error => Promise.reject(error)) // 为了可以在代码中catch到错误信息


        // 响应拦截
        this.service.interceptors.response.use((response: AxiosResponse<any>) => {
            const data = response.data
            const { code } = data

            //判断请求响应码
            if (code != 200) {
                ElMessage.error(data.message)
                return Promise.reject(data)
            }
            //判断后台自定义响应码
            switch (data.data.code) {
                case 401:
                    ElMessage.error(data.data.message)
                    router.replace("/login") //鉴权失败，直接跳转至登录页面
                    return Promise.reject(data);
            }

            return data
        }, error => {
            // 超出 2xx 范围的状态码都会触发该函数。
            ElMessage.error(error.message)
            return Promise.reject(error)
        })
    }
    get<T>(url: string, params?: object, _object = {}): Promise<IResponseData<T>> {
        return this.service.get(url, { params, ..._object })
    }
    post<T>(url: string, params: object, _object = {}): Promise<IResponseData<T>> {
        return this.service.post(url, params, _object)
    }
}

const $http = new Http()

export default $http
