import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

//处理Chrome控制台警告的问题 Added non-passive event listener to a scroll-blocking 'mousewheel' event. Consider marking event handler as 'passive' to make the page more responsive.
import 'default-passive-events'

//引入 element-ui
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'

createApp(App)
    .use(store)
    .use(router)
    .use(ElementPlus)
    .mount('#app')
